
import {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonSearchbar,
    IonButtons,
    IonMenu,
    IonList,
    IonItem,
    IonMenuButton,
    menuController,
    onIonViewDidLeave,
    IonButton,
    IonIcon,
    loadingController,
    IonLabel
} from '@ionic/vue';
import { defineComponent, ref } from 'vue';
import { searchSharp } from 'ionicons/icons';
import NeopleApi from '@/utils/NeopleApi';
import Common from '@/utils/Common';

export default defineComponent({
    components: {
        IonContent,
        IonHeader,
        IonPage,
        IonTitle,
        IonToolbar,
        IonSearchbar,
        IonButtons,
        IonMenu,
        IonList,
        IonItem,
        IonMenuButton,
        IonButton,
        IonIcon,
        IonLabel
    },
    setup() {
        const player = ref('');
        let playerId = '';
        let playerInfo = ref();

        const playerSearch = async () => {
            const loading = await loadingController.create({
                message: '데이터 조회중',
                mode: 'ios'
            });
            loading.present();

            const playerIdJson = await NeopleApi.cyPlayerId({ nickname: encodeURI(player.value) });

            if (Common.isNull(playerIdJson.rows)) {
                playerId = '';
            } else {
                playerId = playerIdJson.rows[0].playerId;
                playerInfo.value = ref(await NeopleApi.cyPlayerInfo({ playerId: playerId })).value;
                console.log(playerInfo.value);

            }

            loading.dismiss();
        };

        onIonViewDidLeave(() => {
            menuController.close('main-menu');
        });

        return {
            searchSharp,
            player,
            playerSearch,
            playerInfo
        }
    }
});
